import React from "react";
import CourseNavigationLesson from "./course-navigation-lesson"

const CourseNavigation = ({ lessons, currentLesson }) => {

  let counter = 1;

  return (
    <div className="lessons-container mt-4">
      <h4>Lessons</h4>
      <div className="lessons-container__list">
        <ul>
          {
            lessons.map(lessonItem => {
              const isCurrentLesson = lessonItem.frontmatter.path === currentLesson.frontmatter.path
              const liClass = isCurrentLesson ? 'now-playing row pb-2 pt-2' : 'row pb-2 pt-2'

              return (
                <li key={lessonItem.id} className={liClass}>
                  <CourseNavigationLesson counter={counter++} lessonItem={lessonItem} isCurrentLesson={isCurrentLesson}/>
                </li>
              )
            })
          }
        </ul>
      </div>
    </div>
  )
}

export default CourseNavigation
