import React from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  FacebookShareButton,
  LinkedinShareButton,
  TwitterShareButton
} from 'react-share';

import '../styles/share.scss';
import {faLinkedin, faTwitter} from "@fortawesome/free-brands-svg-icons";
import {faFacebook} from "@fortawesome/free-brands-svg-icons/faFacebook";

const twitterStyle = {
  border: '1px solid #1da1f2'
};
const facebookStyle = {
  border: '1px solid #3b5998'
};
const linkedInStyle = {
  border: '1px solid #0077b5'
};
const Share = ({ socialConfig, tags }) => (
  <div className="post-social mt-2 mb-2">
    <div className="share-text mr-2 lead">Share:</div>
    <FacebookShareButton url={socialConfig.config.url} style={ facebookStyle } className="button is-outlined is-rounded facebook" >
			<span className="icon">
				<FontAwesomeIcon icon={faFacebook} className="fa"/>
			</span>
      <span className="text">Facebook</span>
    </FacebookShareButton>
    <TwitterShareButton url={socialConfig.config.url}
                        className="button is-outlined is-rounded twitter"
                        title={socialConfig.config.title}
                        via={socialConfig.twitterHandle.split('@').join('')}
                        style={ twitterStyle }
                        hashtags={tags} >
			<span className="icon">
				<FontAwesomeIcon icon={faTwitter} className="fa"/>
			</span>
      <span className="text">Twitter</span>
    </TwitterShareButton>
    <LinkedinShareButton url={socialConfig.config.url} style={ linkedInStyle } className="button is-outlined is-rounded linkedin" title={socialConfig.config.title} >
			<span className="icon">
				<FontAwesomeIcon icon={faLinkedin} className="fa"/>
			</span>
      <span className="text">LinkedIn</span>
    </LinkedinShareButton>
  </div>
);

Share.propTypes = {
  socialConfig: PropTypes.shape({
    twitterHandle: PropTypes.string.isRequired,
    config: PropTypes.shape({
      url: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
    }),
  }).isRequired,
  tags: PropTypes.arrayOf(PropTypes.string),
};
Share.defaultProps = {
  tags: [],
};

export default Share;
