import React from "react"
import {Link} from "gatsby-plugin-modal-routing";
import PropTypes from "prop-types";

const FooterNav = ({ prevLink, nextLink, prevLinkHome, currentPath }) => {
    return (
        <div className="navbar lesson-nav">
            <div className="col left">
                {prevLink !== '' ?
                    <Link to={prevLink}
                          className="btn btn-block text-right btn-left"
                          role="button"
                          aria-pressed="true">
                        <div className="button-label">
                            <span className="material-icons arrow-icon arrow-left">keyboard_arrow_left</span>
                            <span className="label-text">Previous Lesson</span>
                        </div>
                    </Link> :
                    <Link to={prevLinkHome}
                          className="btn btn-block text-right btn-left"
                          role="button"
                          aria-pressed="true">
                        <div className="button-label">
                            <span className="material-icons arrow-icon arrow-left">keyboard_arrow_left</span>
                            <span className="label-text">Back to Course</span>
                        </div>
                    </Link>
                }
            </div>
            <div className="col right">
                {nextLink !== '' ?
                    <Link to={nextLink}
                          className="btn btn-block btn-right text-left"
                          role="button"
                          aria-pressed="true">
                        <div className="button-label">
                            <span className="label-text">Next Lesson</span>
                            <span className="material-icons arrow-icon arrow-right">keyboard_arrow_right</span>
                        </div>
                    </Link> :
                    <Link to="/subscribe"
                          className="btn btn-block btn-right text-left" role="button" aria-pressed="true"
                          asModal state={{
                            closeTo: currentPath,
                            noScroll: true
                        }}>
                        <div className="button-label">
                            <span className="label-text">Next Lesson</span>
                            <span className="material-icons arrow-icon arrow-right">keyboard_arrow_right</span>
                        </div>
                    </Link>
                }
            </div>
        </div>
    )
}

FooterNav.propTypes = {
    prevLink: PropTypes.string,
    nextLink: PropTypes.string,
    prevLinkHome: PropTypes.string,
    currentPath: PropTypes.string
}

export default FooterNav