import React from "react"
import Newsletter from "../newsletter"

const LessonSidebar = ({ lesson }) => {

  return (
    <div className="sidebar mt-4">
      <div className="sidebar-content p-3 mt-4">
        <div className="row ml-1 lesson-resources">
          <span className="material-icons mr-2 pt-1">cloud_download</span>
          <h4>Lesson Resources</h4>
        </div>
        {lesson.frontmatter.lesson_link === undefined ?
          <ul className="pt-1">
            <li><a href={lesson.frontmatter.lesson_link}
                   className="download-code"
                   target="_blank" rel="noopener noreferrer">{lesson.frontmatter.lesson_label}</a></li>
          </ul>
          : 'There are no resources for this lesson.'
        }
      </div>
      <div className="p-4 mt-3 newsletter-container">
        <Newsletter />
      </div>
    </div>
  )
}

export default LessonSidebar
