import React from 'react'
import {graphql} from 'gatsby'

import Layout from '../components/layout'
import SEO from '../components/seo'
import FooterNav from '../components/lesson/footer-nav'
import CourseNavigation from '../components/lesson/course-navigation'
import LessonSidebar from '../components/lesson/sidebar'
import Share from '../components/share'

import '../styles/lesson.scss'

export default function Template({ data, pageContext }) {
    const {
        site: {
            siteMetadata: { url, twitterHandle },
        },
    } = data;

    const {LessonQuery: lesson} = data;
    const {CourseQuery: course} = data;
    const lessons = pageContext.lessons;
    const tags = lesson.frontmatter.tags ? lesson.frontmatter.tags : [];
    const prev = pageContext.prev;
    const next = pageContext.next;

    const courseTitle = course.frontmatter.title

    const title = lesson.frontmatter.title + ' | ' + courseTitle
    const description = lesson.frontmatter.description || lesson.frontmatter.title

    return (
        <Layout cssCLass="lesson">
            <SEO title={ title } description={ description } />
            <div className="lesson-header pb-5 pt-5">
                <div className="container">
                    <h1 className="mt-4 mb-2">{courseTitle}</h1>
                    <p className="lessons-number lead mb-4">{lessons.length} lesson{lessons.length > 1 ? 's' : ''}</p>
                </div>
            </div>
            <div className="container-fluid mb-5 mt-0">
                <div className="content-container pb-2">
                    <div className="row">
                        <div className="col-md-8 main mt-0">
                            <div dangerouslySetInnerHTML={{__html: lesson.html}} />
                            <Share socialConfig={{
                                twitterHandle,
                                config: {
                                  url: `${url}${lesson.frontmatter.path}`,
                                  title,
                                },
                              }}
                               tags={tags} />
                        </div>
                        <div className="col-md-4 right-part">
                          <CourseNavigation lessons={lessons} currentLesson={lesson} />
                          <LessonSidebar lesson={lesson} />
                        </div>
                    </div>
                </div>
            </div>
            <FooterNav
                prevLink={prev ? prev.frontmatter.path : ''}
                nextLink={next ? next.frontmatter.path : ''}
                prevLinkHome={course.frontmatter.path}
                currentPath={lesson.frontmatter.path}
            />
        </Layout>
    )
}

export const pageQuery = graphql`
  query($path: String!, $courseCode: String!) {
    site {
      siteMetadata {
        url
        twitterHandle
      }
    }
    LessonQuery: markdownRemark(
        frontmatter: { 
          path: { eq: $path } 
        }
    ) {
      html
      frontmatter {
        path
        title
        lesson_link
        lesson_label
        description
      }
    }
    CourseQuery: markdownRemark(
        frontmatter: { 
          code: { eq: $courseCode } 
        }
    ) {
      frontmatter {
        title
      }
    }
  }
`
